* {
  box-sizing: border-box;
}

body {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  height: 100vh;
  margin: 0;
  display: flex;
}

.watch {
  border: 3px dashed #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.watch__center {
  z-index: 3;
  background-color: orange;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  margin: auto;
  position: absolute;
  inset: 0;
}

.watch__hour-hand {
  z-index: 2;
  background-color: #00f;
  width: 9px;
  margin: auto;
  animation: 60s linear infinite rotate-hand;
  position: absolute;
  top: 39.8vh;
  left: 0;
  right: 0;
}

.watch__hour-hand:before {
  content: "";
  z-index: 2;
  background: #00f;
  width: 9px;
  height: 21vmin;
  position: absolute;
}

.watch__minute-hand {
  z-index: 1;
  background-color: green;
  width: 3px;
  margin: auto;
  animation: 5s linear infinite rotate-hand;
  position: absolute;
  top: 39.8vh;
  left: 0;
  right: 0;
}

.watch__minute-hand:before {
  content: "";
  z-index: 1;
  background-color: green;
  width: 3px;
  height: 39.6vmin;
  position: absolute;
}

@keyframes rotate-hand {
  to {
    transform: rotate(360deg);
  }
}

